	module.exports = {"data":{"wordpressWpLessons":{"id":"0b212e66-89a7-5279-804f-70beb28b4a2f","wordpress_id":85,"title":"We Are All Makers","slug":"maker-warm-up","content":"<h2>Goals and Overview</h2>\n<p>Are you a <em><strong>maker?</strong></em> What do you like to <b><i>make</i></b><i></i>? In this lesson, we will learn a little about what a maker is and enjoy some videos of makers and the things they make. You will also get a chance to make something of your own in your first maker challenge!</p>\n<h3>Goals:</h3>\n<ul>\n<li style=\"list-style-type: none;\">\n<ul>\n<li>Introduction to the Maker Movement</li>\n<li>Understand what a  &#8220;maker&#8221; is</li>\n<li>Identify qualities of a maker</li>\n<li>Make something!</li>\n</ul>\n</li>\n</ul>\n<hr />\n<h2>What is a Maker?</h2>\n<p>You will hear us talk a lot about makers during this program. Makers are people who make or produce things. Here at the Tech Center, we consider ourselves a youth makerspace, which is a place where makers go to learn new skills and work on their projects. Essentially, it&#8217;s a space to make! Our primary focus is on making things related to technology, but you will see things of all types being made here.</p>\n<h3><span style=\"color: #0091c9;\">Activity: What is a Maker?</span></h3>\n<p>Please take a post-it note (or more than one!) and write down a response to this prompt:</p>\n<ul>\n<li><strong>A Maker Is&#8230;</strong></li>\n</ul>\n<p>When you have written out your responses, please add them to the poster.<br />\nThis poster will be placed in the MegaLab as a constant inspiration and reminder that we&#8217;re all part of the Maker Movement!</p>\n<h2>Inspiring Maker Videos</h2>\n<p>To provide you with some inspiration about makers, we have put together a list of videos which highlight the culture and ideas surrounding the maker movement. We will watch the first video together and reflect on it, but please take a look at the other videos when you have time. We hope that these will inspire you and help you better understand the type of activities we are pursuing.</p>\n<p><iframe width=\"500\" height=\"281\" src=\"//player.vimeo.com/video/66162292?color=f9dc39&amp;title=0&amp;byline=0&amp;portrait=0\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe><br />\n<a href=\"http://vimeo.com/66162292\">We Are Makers</a> from <a href=\"http://vimeo.com/learningstudio\">Learning Studio</a> on <a href=\"https://vimeo.com\">Vimeo</a></p>\n<p><iframe width=\"480\" height=\"360\" src=\"//www.youtube.com/embed/mlrB6npbwVQ?rel=0\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe></p>\n<p><iframe width=\"560\" height=\"315\" src=\"//www.youtube.com/embed/_otrgJ8Lmx4?rel=0\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe></p>\n<p><iframe width=\"560\" height=\"315\" src=\"//www.youtube.com/embed/faIFNkdq96U?rel=0\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe></p>\n<p><iframe width=\"560\" height=\"315\" src=\"//www.youtube.com/embed/yOc9mFvusDg?rel=0\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe></p>\n<p><iframe width=\"560\" height=\"315\" src=\"//www.youtube.com/embed/XOLOLrUBRBY?rel=0\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe></p>\n<p>We can&#8217;t wait to see what YOU make!</p>\n"}},"pathContext":{}}

